import PropTypes from "prop-types";
import { useEffect, useState, useRef } from "react";
import { useParams } from "react-router-dom";
import '../styles/MasterHeader.css'
import { Transition } from 'react-transition-group';
import { Link } from "react-router-dom";
import axios from 'axios';

function MasterHeader() {

    async function onClick() {
        await handleLogout();
    }

    async function handleLogout() {
        await axios.post('/logout', 
        // await axios.post('/api/logout', 
            
            { "Content-Type": "application/json", withCredentials: true }   //withCredentials: true:   (JSESSIONID)  
        );
            window.location.href = '/login-page';  //  URL .        
    }

    const onClickIntroduceButton = () => {
        console.log("   ");
    }


    return (
        <div className="mh-container">
            <h1 button className="mh-title-button"></h1>
            <Link to="/about"  style={{ textDecoration: "none", color: "black"}}>
                <button className="mh-introduce-button" onClick={onClickIntroduceButton}> </button>
            </Link>
            <Link to="/login-page"  style={{ textDecoration: "none", color: "black"}}>
                <button></button>
            </Link>
                <button onClick={onClick}></button>
        </div>
    );
}

export default MasterHeader;
